import request from '@/utils/request'
// 拉取存储库
export function fetchImage(requestImage) {
  return request.scaner({
    url: '/scanner',
    method: 'post',
    data: requestImage
  })
}

export function fetchIndex(imageId) {
  return request.scaner({
    url: `/scanner/${imageId}/index`,
    method: 'get'
  })
}

export function fetchSecurity(imageId, requestSecurity) {
  return request.scaner({
    url: `/scanner/${imageId}/security`,
    method: 'post',
    data: requestSecurity
  })
}



