<template>
  <div class="main">
    <Navbar/>
    <SearchImage class="search"/>
    <div class="btn-right">

    </div>
    <section class="table">
      <div class="tab-list">
        <div class="tabs">
          <div class="tab-item" v-for="(item,index) in editableTabs"
               :class="{on : index === tabIndex}"
               :key="index" @click="handleChangeImage(index)">
            {{item.name}}
          </div>
          <div class="tab-item" @click="handleShowForm('add')">
            <i class="el-icon-circle-plus-outline" style="font-size: 18px;"/>
          </div>
        </div>
        <div class="tab-right">
          <el-button slot="label" style="font-size: 16px;" size="small"
                     type="primary" icon="el-icon-edit" @click="dialogFormVisible = true">
            安全评分
          </el-button>
          <el-button slot="label" style="font-size: 16px;" size="small"
                     icon="el-icon-setting" type="primary" @click="handleShowForm">
            仓库管理
          </el-button>
        </div>
      </div>
      <ImgTable :repository="requestRepository" />
    </section>
    <div class="form" v-show="showForm"
         v-loading="fromLoading" element-loading-spinner="el-icon-loading" element-loading-background="rgba(0, 0, 0, 0.8)" element-loading-text="正在扫描中…">
      <div class="form-side">
        <ul>
          <li class="item" v-for="(item, idx) in tmpEditableTabs" :key="idx"
              :class="{active: currentIndex == idx}" @click="handleSelect(item, idx)">
            <span :class="{ok:item.isConnect}"></span>{{item.name}}
          </li>
        </ul>
        <div class="btn-add item" @click="handleAdd">
          <i class="el-icon-circle-plus-outline"/>
        </div>
      </div>
      <div class="form-content">
        <el-form :model="form" :rules="rules" ref="ruleForm" size="mini">
          <el-form-item label="name" prop="name" :label-width="formLabelWidth">
            <el-input v-model="form.name" auto-complete="off"/>
          </el-form-item>
          <el-form-item label="port" prop="port" :label-width="formLabelWidth">
            <el-input v-model="form.port"/>
          </el-form-item>
          <el-form-item label="是否https" prop="isHttps" :label-width="formLabelWidth">
            <el-switch v-model="form.isHttps">
            </el-switch>
          </el-form-item>
          <el-form-item label="是否认证" prop="isAuth" :label-width="formLabelWidth">
            <el-switch v-model="form.isAuth">
            </el-switch>
          </el-form-item>
          <el-form-item label="用户名" prop="username" :label-width="formLabelWidth">
            <el-input v-model="form.username" auto-complete="off"/>
          </el-form-item>
          <el-form-item label="密码" prop="password" :label-width="formLabelWidth">
            <el-input v-model="form.passwd" auto-complete="off"/>
          </el-form-item>
          <el-form-item label="repository" prop="repository"
                        :label-width="formLabelWidth" >
            <el-input v-model="form.repository" auto-complete="off"
                      :disabled="form.isCanEdit? false:true"/>
          </el-form-item>
        </el-form>
        <div class="btn">
          <el-button @click="handleSave"
                     size="small" type="primary" icon="el-icon-circle-check-outline">
            保存
          </el-button>
          <el-button @click="handleRemove" v-show="!form.isCanEdit"
                     size="small" type="danger" icon="el-icon-delete">
            删除
          </el-button>
          <el-button @click="handleCancel"
                     type="info" size="small" icon="el-icon-circle-close-outline">
            取消
          </el-button>
        </div>
      </div>
    </div>
    <el-dialog title="安全权重" :visible.sync="dialogFormVisible" width=" 30% ">
      <el-form :model="score">
        <el-form-item label="高危" :label-width="ScoreLabelWidth">
          <el-input class="item-input" v-model="score.high" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="中危" :label-width="ScoreLabelWidth">
          <el-input class="item-input" v-model="score.medium" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="低危" :label-width="ScoreLabelWidth">
          <el-input class="item-input" v-model="score.low" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="轻危" :label-width="ScoreLabelWidth">
          <el-input class="item-input" v-model="score.negligible" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="未知" :label-width="ScoreLabelWidth">
          <el-input class="item-input" v-model="score.unknown" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer btn-footer">
        <el-button type="primary" @click="handleSaveScore"
                   icon="el-icon-circle-check-outline">确 定</el-button>
        <el-button @click="dialogFormVisible = false"
                   icon="el-icon-circle-close-outline">取 消</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script type="text/ecmascript-6">
  import * as scanerApi from '@/api/transactionScaner2'
  import Navbar from 'components/navbar'
  import SearchImage from './components/searchImage'
  import ImgTable from './components/imageTable'
  import AddImage from './components/addImage'

  export default {
    name: 'index',
    components: {
      Navbar,
      SearchImage,
      ImgTable,
      AddImage
    },
    data() {
      // const validatePass = (rule, value, callback) => {
      //   if (value === '') {
      //     callback(new Error('请输入密码'))
      //   } else {
      //     if (this.form.checkPass !== '') {
      //       this.$refs.ruleForm.validateField('checkPass')
      //     }
      //     callback()
      //   }
      // }
      return {
        rules: {
          repository: [
            { required: true, message: '请填写repository', trigger: 'blur' }
          ]
        },
        currentIndex: 0,
        formLabelWidth: '120px',
        ScoreLabelWidth: '100px',
        activeName: '',
        showForm: false,
        showScore: false,
        dialogVisible: false,
        editableTabsValue: 0,
        editableTabs: [],
        tmpEditableTabs: [],
        tabIndex: 0,
        score: {},
        dialogFormVisible: false,
        fromLoading: false,
        form: {},
        showType: ''
      }
    },
    // watch: {
    //   handleEditFormVisible() {
    //     this.$refs.ruleForm.clearValidate()
    //   }
    // },
    mounted() {
      this.getImageList()
      this.getScoreInfo()
      document.addEventListener('click', this.handleFormClose, false)
    },
    computed: {
        requestRepository() {
          if (this.editableTabs[this.tabIndex]) {
            return this.editableTabs[this.tabIndex].repository + ':' + this.editableTabs[this.tabIndex].port
          }
          return ''
        }
    },
    methods: {
      changeImage() {
        this.form = this.tmpEditableTabs[this.currentIndex]
      },
      handleChangeImage(index) {
        this.tabIndex = index
      },
      getImageList() {
        scanerApi.getImageList().then(res => {
          this.editableTabs = res.data.data
          this.tmpEditableTabs = JSON.parse(JSON.stringify(this.editableTabs))
        })
      },
      getScoreInfo() {
        scanerApi.getScore().then(res => {
          this.score = res.data.data
        })
      },
      handleSaveScore() {
        this.dialogFormVisible = false
        scanerApi.updateScore(this.score).then(res => {
          let type = 0
          if (res.data.code === 0) {
            type = 1
            this.score = res.data.data
          }
          this.isSuccess(type)
        })
      },
      handleAdd() {
        let editableTabs = JSON.parse(JSON.stringify(this.editableTabs))
        editableTabs.push({
          port: 8000,
          name: '新仓库',
          username: '',
          passwd: '',
          isHttps: false,
          isAuth: false,
          repository: '',
          isCanEdit: true
        })
        this.currentIndex = editableTabs.length - 1
        this.tmpEditableTabs = editableTabs
        this.changeImage()
      },
      handleShowForm(type) {
        this.showForm = true
        if (type === 'add') {
          this.currentIndex = this.editableTabs.length
          this.handleAdd()
          return
        }
        this.tmpEditableTabs = JSON.parse(JSON.stringify(this.editableTabs))
        this.currentIndex = this.tabIndex
        this.form = this.tmpEditableTabs[this.tabIndex]
        console.log('form', this.form)
      },
      isSuccess(type) {
        if (type === 1) {
          this.$message({
            showClose: true,
            message: '保存成功',
            type: 'success'
          })
        } else {
          this.$message({
            showClose: true,
            message: '保存失败',
            type: 'error'
          })
        }
      },
      handleSave() {
        this.fromLoading = true
        let form = this.form
        if (form.isCanEdit) {
          form.isCanEdit = undefined
          scanerApi.createImage(form).then(res => {
            let type = 0
            if (res.data.code === 0) {
              type = 1
              this.form = res.data.data
              this.tmpEditableTabs[this.currentIndex] = this.form
              this.editableTabs = JSON.parse(JSON.stringify(this.tmpEditableTabs))
            } else {
              //
            }
            this.isSuccess(type)
            this.fromLoading = false
          })
        } else {
          scanerApi.updateImage(form).then(res => {
            let type = 0
            if (res.data.code === 0) {
              type = 1
              // console.log('res', res.data)
              this.form = res.data.data
            }
            this.tmpEditableTabs[this.currentIndex] = this.form
            this.editableTabs = JSON.parse(JSON.stringify(this.tmpEditableTabs))
            this.isSuccess(type)
            this.fromLoading = false
          })
        }
      },
      handleRemove() {
        let repository = this.form.repository
        console.log('开始删除')
        scanerApi.deleteImage(repository).then(res => {
          console.log('请求成功')
          if (res.data.code === 0) {
            let list = this.tmpEditableTabs
            list.splice(this.currentIndex, 1)
            this.editableTabs = JSON.parse(JSON.stringify(this.tmpEditableTabs))
            if (this.currentIndex === this.tmpEditableTabs.length) this.currentIndex -= 1
            this.changeImage()
            this.$message({
              showClose: true,
              message: '删除成功',
              type: 'success'
            })
          } else {
            this.$message({
              showClose: true,
              message: '删除失败',
              type: 'error'
            })
          }
        })
      },
      handleCancel() {
        this.$refs.ruleForm.clearValidate()
        this.showForm = false
      },
      // 点击空白处,form消失
      handleFormClose(evt) {
        if (!this.$el.contains(evt.target)) {
          this.$refs.ruleForm.clearValidate()
          this.showForm = false
        }
      },
      handleSelect(item, idx) {
        this.form = item
        this.currentIndex = idx
      }
    },
    destroyed() {
      document.removeEventListener('click', this.handleFormClose, false)
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  .main
    position relative

  .search
    margin: 0 auto
    width: 100%
    height: 450px
    background: url(./img/bg.jpg) center
    background-size: cover
    overflow: hidden

  .table
    margin-top 50px
    width: 93%
    margin: 50px auto
    border 1px solid #999

    .tab-list {
      padding 5px 5px 0px 5px
      overflow hidden

      .tabs {
        float left
        display flex

        .tab-item {
          &.on {
            background: #fff
            color #3E7AF6
          }
          width 110px
          height 32px
          font-size 14px
          border 1px solid #ccc
          border-bottom none
          cursor pointer
          margin-right 1px
          padding 0 15px
          text-align center
          line-height 34px
          background #ccc
          border-radius 5px 5px 0px 0px
          color #040404
        }
      }

      .tab-right {
        float right
      }
    }

  .repository
    position: fixed
    top 204px
    left 50%
    margin-left -330px
    z-index: 100

  .btn-right
    float right

  .form
    width: 660px;
    height: 630px;
    background: rgba(255, 255, 255, 1)
    border: 1px solid rgba(62, 122, 246, 1)
    box-shadow: 0px 0px 50px 0px rgba(0, 0, 0, 0.4)
    position absolute
    top: 200px
    left 50%
    margin-left -330px
    z-index 1000

    &-side
      width 142px
      height 100%
      background-color #E6E6E6
      padding-top 31px

      .active
        color: #3E7AF6 !important
        background: #fff

      .item
        width: 142px
        height: 42px
        font-size 16px
        font-weight bold
        color: rgba(102, 102, 102, 1)
        text-align center
        line-height 42px
        cursor pointer
        span{
          margin-right 3px
          display inline-block
          width 10px
          height 10px
          border-radius 50%
          background red
          &.ok{
            background limegreen
          }
        }
      .btn-add
        position absolute
        bottom 0
        left 0
        color: #fff
        font-size 20px
        background: rgba(62, 122, 246, 1)

    &-content
      height 100%
      position absolute
      top: 50px
      margin-left 150px

      .btn
        margin 100px 80px
  .btn-footer
    text-align center
  .item-input
    width: 80%
</style>



