<template>
  <div class="image-table">
    <h1>镜像列表</h1>
    <div class="image-btn">
      <el-button class="btn-new" @click="dialogVisible=true">
        <i class="icon-hao">&#xe66e;
        </i>新建镜像
      </el-button>
      <el-dialog
        title="提示"
        :visible.sync="dialogVisible"
        width="30%">
        <div>请输入需要添加的镜像(例如：(image:tag))，确定添加请按确定按钮。
          有些镜像特别大，所以会出现刷新后没有出现镜像，但是镜像库是存在下载的镜像，
          您需要多刷新几次，就能找到添加的镜像。
        </div>
        <el-input style="margin-top: 20px" v-model="addImageList[0].addImageName"></el-input>
        <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="handleNewImage">确 定</el-button>
        </span>
      </el-dialog>
    </div>
    <el-table class="add-image" :data="addImageList" v-show="tableShow">
      <el-table-column label="存储库" show-overflow-tooltip align="center" min-width="40">
        <template slot-scope="props">
          <i>{{props.row.addImageName}}</i>
        </template>
      </el-table-column>
      <el-table-column label="下载进度" show-overflow-tooltip align="center" min-width="200">
        <template slot-scope="props">
          <el-progress :text-inside="true" :stroke-width="20"
                       :percentage="props.row.current" status="exception"></el-progress>
        </template>
      </el-table-column>
      <el-table-column label="操作" min-width="30">
        <template slot-scope="props">
          <el-button @click="handleImageDelete(props.row)"
                     type="text" size="small">删除
          </el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script type="text/ecmascript-6">
  import * as apiScannerAddImage from '@/api/transactionScanerAddImage'

  export default {
    data() {
      return {
        dialogVisible: false,
        addImageList: [{
          addImageName: '',
          current: 0
        }],
        tableShow: false,
        getProVal: ''
      }
    },

    mounted() {
      window.addEventListener('keyup', event => {
        if (event.keyCode === 13 && this.dialogVisible === true) {
          this.handleNewImage()
        }
      })
    },
    beforeDestroy() {
      clearInterval(this.getProVal)
    },
    methods: {
      getProgressAndSetTime(value) {
        // 获得镜像下载进度
        setTimeout(() => {
          this.getProVal = setInterval(() => {
            apiScannerAddImage.getProgress(value).then(proRes => {
              switch (proRes.data.code) {
                case 101:
                  this.tableShow = true
                  this.addImageList[0].current = proRes.data.current
                  this.$notify.info({
                    title: '消息',
                    message: '等待请求资源',
                    duration: 1000
                  })
                  break
                case 204:
                  this.tableShow = true
                  if (!isNaN(proRes.data.current)) {
                    this.addImageList[0].current = proRes.data.current
                  }
                  break
                case 200:
                  this.tableShow = false
                  this.$notify.info({
                    title: '消息',
                    message: '下载完成正在扫描',
                    type: 'success',
                    duration: 1000
                  })
                  // 三分钟后重新扫描镜像，5秒后刷新页面
                  apiScannerAddImage.reloadPullImage(this.addImageList[0].addImageName)
                  setTimeout(() => {
                    location.reload()
                  }, 10000)
                  clearInterval(this.getProVal)
                  break
                case 201:
                  this.tableShow = false
                  this.$notify.info({
                    title: '消息',
                    message: '本地存在',
                    type: 'warning',
                    duration: 1000
                  })
                  clearInterval(this.getProVal)
                  break
                default:
                  this.tableShow = false
                  this.$notify.info({
                    title: '消息',
                    message: '用户输入错误',
                    type: 'error',
                    duration: 1000
                  })
                  clearInterval(this.getProVal)
              }
            })
          }, 1000)
        }, 100)
      },
      handleNewImage() {
        this.dialogVisible = false
        apiScannerAddImage.pullImage(this.addImageList[0].addImageName).then(res => {
          console.log(res)
        })
        this.getProgressAndSetTime(this.addImageList[0].addImageName)
      }
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
  .image-table
    width: 93%
    margin: 0 auto

    h1
      margin: 58px 0 24px 0px

    .image-btn
      float: right
      padding: 0

      .btn-new
        margin: 45px 0px 0 15px
        color: #FFFFFF
        background: #3E7AF6
        width: 100px
        height: 34px
        padding: 0 0 0 0

      .btn-new:hover
        background: #2360DB

  .image-table .image-btn[data-v-5081db52]
    white-space normal

  .add-image
    margin-bottom 2em
</style>




