<template>
  <div class="search">
    <div class="search-wrapper">
      <p class="search-box">
        <i class="iconfont icon-search">&#xe665;</i>
        <span class="split-line">|</span>
        <input class="search-text clear-edge" type="text"
               placeholder="搜索镜像（nginx,mysql,ubutu..." v-model="content">
        <button class="search-btn clear-edge" @click="handleFetchImage">搜索</button>
      </p>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  import * as apiScanner from '@/api/transactionScaner'

  import { mapGetters } from 'vuex'

  export default {
    name: 'search',
    data() {
      return {
        content: ''
      }
    },
    mounted() {
      window.addEventListener('keyup', (event) => {
        if (event.keyCode === 13 && this.content !== '') {
          this.handleFetchImage()
        }
      })
    },
    computed: {
      ...mapGetters(['requestImage'])
    },
    methods: {
      handleFetchImage() {
        this.$store.commit('setMustValue', this.content)
        apiScanner.fetchImage(this.requestImage).then(res => {
          this.$store.commit('setImageList', res.data.hits)
          this.$store.commit('setPaginationTotal', res.data.total)
        })
      }
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  .search-wrapper
    margin: 0 auto
    width: 63.5%
    position: relative
    top: 50%

    .search-box
      height: 55px
      line-height: 51px
      background: #2D3C54
      border-radius: 5px
      -moz-border-radius: 5px /* Old Firefox */
      transition: 0.7s
      padding: 0 0 0 15px

      &:hover
        box-shadow: 0 2px 12px 0 rgba(0, 0, 0, .6)

      #icon-search
        background-position: -0px -35px
        width: 22px
        height: 22px
        margin: 0 10px 0 16px

      .split-line
        color: #818796
        font-size: 3.0em
        margin: 0px 18px 0px 1px

      .search-text
        color: #CCCCCC
        background: #2D3C54
        width: 50%
        font-size: 2em
        display: inline-block
        vertical-align: middle

      .search-btn
        margin-top: 11px
        margin-right: 12px
        float: right
        width: 100px
        cursor: pointer
        font-size: 1.4em
        color: #E6E6E6
        line-height: 32px
        background: #3F5067
        border-radius: 4px
        -moz-border-radius: 4px

  /* Old Firefox */


  .icon-search
    color: #CCCCCC
    font-size: 2.4em
</style>



