import request from '@/utils/request'

// 新建镜像
export function pullImage(name) {
  return request.scanerAddImage({
    url: '/images_pull',
    method: 'get',
    params: {
      name
    }
  })
}

// 删除镜像
export function deleteImage(repotags) {
  return request.scanerAddImage({
    url: '/delete',
    method: 'delete',
    data: {
      repotags
    }
  })
}

// 拉取镜像重新扫描
export function reloadPullImage(name) {
  return request.scanerAddImage({
    url: '',
    method: 'get',
    params: {
      name
    }
  })
}

// 拉取进度条
export function getProgress(name) {
  return request.scanerAddImage({
    url: '/progress',
    method: 'get',
    params: {
      name
    }
  })
}



