<template>
  <div class="image-table">
    <el-table :data="imageList" @sort-change="handleSort">
      <!--<el-table-column prop="image.raw" label="存储库" sortable="custom"-->
                       <!--show-overflow-tooltip align="center" min-width="80">-->
        <!--<template slot-scope="props">-->
          <!--<router-link :to="{ path: 'scannerView', query: { image_id: props.row._id}}">-->
            <!--<i>{{props.row._source.image}}</i>-->
          <!--</router-link>-->
        <!--</template>-->
      <!--</el-table-column>-->
      <!--<el-table-column label="注册表/类型" show-overflow-tooltip align="center" min-width="40">-->
        <!--<template slot-scope="props">-->
          <!--<i class="iconfont icon-docker index-warning"> &#xe672;</i>-->
          <!--官方-->
        <!--</template>-->
      <!--</el-table-column>-->
      <el-table-column prop="image" label="镜像" sortable="custom"
                       show-overflow-tooltip align="center" min-width="80">
        <template slot-scope="props">
          <router-link :to="{ path: 'scannerView',
          query: { repository: props.row.repository,tag:props.row.tag,image:props.row.image}}">
            <span style="margin-left: 10px">
              {{props.row.image}}:{{props.row.tag}}
            </span>
          </router-link>
        </template>
      </el-table-column>
      <el-table-column
        label="安全问题" show-overflow-tooltip align="center" min-width="100">
        <template slot-scope="props">
          <block v-if="(props.row.high!=-1)||(props.row.medium!=-1)
        ||(props.row.low!=-1)||(props.row.negligible!=-1)||(props.row.unknown!=-1)">
          <i class="iconfont index-warning icon-red">
            &#xe638;</i>
          <span>{{props.row.high}}</span>
          <i class="iconfont index-warning icon-orange">
            &#xe638;</i>
          <span>{{props.row.medium}}</span>
          <i class="iconfont index-warning icon-yellow">
            &#xe638;</i>
          <span>{{props.row.low}}</span>
          <i class="iconfont index-warning icon-blue">
            &#xe638;</i>
          <span>{{props.row.negligible}}</span>
          <i class="iconfont index-warning icon-gary">
            &#xe638;</i>
          <span>{{props.row.unknown}}</span>
          </block>
          <block v-else>
            <span>待扫描</span>
          </block>
        </template>
        <!--<template v-else slot-scope="props">-->
          <!--<span class="progress-bar progress-bar-success progress-bar-striped"-->
                <!--role="progressbar" style="width: 40%"-->
                <!--aria-valuenow="40" aria-valuemin="0" aria-valuemax="100">-->
            <!--<span class="sr-only">待扫描</span>-->
          <!--</span>-->
        <!--</template>-->
      </el-table-column>
      <el-table-column prop='score' label="安全评分" sortable="custom">
        <template slot-scope="props">
          <i  v-if="props.row.score != -1">{{Math.round(props.row.score * 100)}}</i>
          <span v-else class="back">
            <span class="progress"></span>
          </span>
        </template>
      </el-table-column>
      <el-table-column prop="updated_at" label="扫描时间" sortable="custom"
                       show-overflow-tooltip align="center" min-width="80">
        <template slot-scope="props">
          <i>{{new Date(props.row.updated_at).toLocaleString('chinese',
            {hour12: false})}}</i>
        </template>
      </el-table-column>
      <!--<el-table-column prop="dockerImage.Size" label="SIZE" sortable="custom"-->
                       <!--show-overflow-tooltip align="center" min-width="40">-->
        <!--<template slot-scope="props">-->
          <!--<i v-if="(Math.round(props.row._source.dockerImage.Size / 1024/1024))<1024">-->
            <!--{{Math.round(props.row._source.dockerImage.Size / 1024/1024)}}MB-->
          <!--</i>-->
          <!--<i v-else>{{(
          props.row._source.dockerImage.Size / 1024/1024/1024).toFixed(1)}}GB</i>-->
        <!--</template>-->
      <!--</el-table-column>-->
      <!--<el-table-column label="操作" min-width="40">-->
        <!--<template slot-scope="props">-->
          <!--<el-button @click="handleImageDelete(props.row._source.image)"-->
                     <!--type="text" size="small">删除-->
          <!--</el-button>-->
        <!--</template>-->
      <!--</el-table-column>-->
    </el-table>
    <section class="pagination">
      <el-pagination
        background
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :page-sizes="[10, 20, 30, 40]"
        :page-size="100"
        layout="total, sizes, prev, pager, next, jumper"
        :total="paginationTotalMain">
      </el-pagination>
    </section>
  </div>
</template>

<script type="text/ecmascript-6">
  // eslint-disable-next-line
  import io from 'socket.io-client'
  import * as apiScanner from '@/api/transactionScaner'
  // import * as apiScannerAddImage from '@/api/transactionScanerAddImage'

  import { mapGetters } from 'vuex'
  import ElSlPanel from 'element-ui/packages/color-picker/src/components/sv-panel'

  export default {
    components: { ElSlPanel },
    props: ['repository'],
    data() {
      return {
        imageNameList: [],
        imageRequestData: {
          repository: '192.168.10.118:5000',
          pagination: {
            from: 0,
            size: 5
          },
          sort: {
            field: 'created_at',
            order: 'asc'
          },
          search: []
        }
      }
    },
    computed: {
      ...mapGetters(['requestImage', 'imageList', 'paginationTotalMain'])
    },
    watch: {
      repository() {
        this.$store.commit('setRepository', this.repository)
        this.handleFetchImage()
      }
    },
    methods: {
      handleFetchImage() {
        // Todo
        // apiScanner.fetchImage({
        //   repository: '192.168.10.118:5000',
        //   pagination: {
        //     from: 0,
        //     size: 5
        //   },
        //   sort: {
        //     field: 'created_at',
        //     order: 'asc'
        //   },
        //   search: []
        // })
        //   .then((res) => {
        //     console.log('res', res.data.data.docs)
        //     this.imageNameList = res.data.data.docs
        //     // this.imageList = [...this.requestImage]
        //   })
        apiScanner.fetchImage(this.requestImage).then(res => {
          this.$store.commit('setImageList', res.data.data.docs)
          this.$store.commit('setPaginationTotal', res.data.data.count)
        })
      },
      // 每页条数
      handleSizeChange(val) {
        // 改变vuex的Pagination的size属性
        this.$store.commit('setPaginationSize', val)
        this.handleFetchImage()
      },
      // 当前页
      handleCurrentChange(val) {
        // 改变vuex的Pagination的from属性
        this.$store.commit('setPaginationFrom', (val - 1) * this.requestImage.pagination.size)
        this.handleFetchImage()
      },
      // 删除镜像、延迟刷新
      // handleImageDelete(row) {
      //   apiScannerAddImage.deleteImage(row)
      //   this.$notify({
      //     title: '成功',
      //     message: '正在删除',
      //     type: 'success'
      //   })
        // setTimeout(() => {
        //   location.reload()
        // }, 3000)
      // },
      // 排序
      handleSort(val) {
        // 将字段传入
        console.log(val.prop)
        this.$store.commit('setSortField', val.prop)
        // 将排序方式传入
        this.$store.commit('setSortOrder', val.order.substr(0, val.order.length - 6))
        this.handleFetchImage()
      }
    },
    created() {
      const host = location.host
      console.log('socketHost', host.split(':')[0])
      const socket = io.connect(`http://${host.split(':')[0]}:4001`)
      socket.on('news', (data) => {
        console.log(data)
        this.handleFetchImage()
      })
    },
    mounted() {
      // console.log(this.repository)
      this.$store.commit('setRepository', this.repository)
      this.handleFetchImage()
      //   socket.on('fresh', (data) => {
      //     console.log('data', data)
      //     this.handleFetchImage()
      //   })
      // })
      // const socket = io('http://192.168.3.3:4001')
      // socket.on('fresh', () => {
      //   console.log('connection')
      //   this.handleFetchImage()
      // })
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  .image-table
    margin: 0 auto
    width 99%
  .icon-docker
    color: #3E7AF6

  .index-warning
    font-size: 1.4em

  .icon-red
    color: #FF0000

  .icon-orange
    color: #F27935

  .icon-yellow
    color: #F9BF3B

  .icon-blue
    color: #08B1D5

  .pagination
    float right
    margin 20px
  span
    display inline-block
  .back
    width 150px
    height 14px
    background-color #00c8d2
    border 1px solid rgba(0, 0, 0, 0.1)
    border-radius: 10px
    overflow hidden
  .progress
    width 600px
    height 30px
    background-size 20px 20px
    background-image linear-gradient(-45deg,white 0,white 25%,transparent  0,transparent 50%,white 0,white 75%,transparent 0,transparent 100%)
    animation scorll infinite linear 20s
  @keyframes scorll{
      0%{
        background-position 0px
      }
      100%{
        background-position 600px
      }
    }
</style>



